@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
  --darkBlue: #274472;
  --midnightBlue: #41729F;
  --blueGray: #5885AF;
  --babyBlue: #C3E0E5;
  --lightGreen: #00FF00;
}

/*default font size is 1.3rem for extra small screens (phones)*/
body {
  font-family: 'Roboto Condensed', sans-serif !important;
  background-color: #41729F !important;
  background-color: var(--midnightBlue) !important;
  color: white !important;
  font-size: 1.25rem !important;
}

.section-heading {
  border-bottom: 0.2rem solid #00FF00;
  border-bottom: 0.2rem solid var(--lightGreen);
}

/*when screen gets is greater than size (sm)all = 768 px increase the font size*/
@media (min-width: 768px) {
  body {
    font-size: 1.55rem !important;
  }
}

