.logo {
    width: 8rem;
}

.navbar {
    padding-left: 5rem;
    padding-right: 5rem;
    background-color: var(--darkBlue);
    opacity: .85;
}

nav a.nav-link {
    font-weight: 500;
    letter-spacing: 0.0035rem;
    cursor: pointer;   
}

/*!important overrides bootstrap's rule*/
.navbar-nav > li > a {
    text-transform: uppercase;
    color: white !important;
    margin-left: .6rem;
    margin-right: .6rem;
    border-top: 0.2rem solid var(--darkBlue);
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus, .navbar-nav > li > a:hover{
    border-top: 0.2rem solid var(--lightGreen);
    color: var(--lightGreen) !important;
}

.navbar-toggler {
    color:white !important;
}

.navbar .logo {
    border-top: 0.2rem solid var(--darkBlue);
  }
  
.navbar .logo:hover {
    border-top: 0.2rem solid var(--lightGreen);
}
.header-wrapper {
    position: relative;
    padding-top: 2rem;
    padding-bottom: 2rem;
    background-color: var(--darkBlue);
}

.header-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-left: 5rem;
    padding-right: 5rem;
}

.header-info h1 {
    border-bottom: 0.2rem solid var(--lightGreen);
}

.header-info .typed-text {
    color: var(--lightGreen);
}
.about-wrapper {
    position: relative;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.about-header {
    text-align: center;
}

.about-writing {
    text-align: justify;
}

.about-writing a:link, .about-writing a:visited, .about-writing a:active{
    color: white;
    font-weight: 900;
}

.about-writing a:hover {
    color: var(--midnightBlue);
    background-color: white;
}

.about-picture {
    display: flex;
    justify-content: center;
}

.selfpicture {
    border-radius: 1rem !important;
}
.tools-wrapper {
    position: relative;
    text-align: center;
    background-color: var(--darkBlue);
    padding-top: 2rem;
}

.tools-info .tools {
    text-align: center;
}

.experience-wrapper {
    position: relative;
    text-align: center;
    padding-top: 2rem;
    background-color: var(--darkBlue);
}

.experience-info {
    display: flex;
    justify-content: center;
}

.experience-info .tab-pane{
    text-align: start;
    font-size: 1.35rem;
    background-color: var(--midnightBlue);
    padding-right: 1rem;
    border-radius: 1rem;
}

.experience-places {
    padding-top: .8rem;
}

.tab-pane h3{
    text-align: center;
    color: var(--lightGreen);
    justify-content: center;
}

.location {
    font-size: 1.2rem;
    color: white;
}

.tab-pane {
    padding-top: .5rem;
    padding-bottom: .5rem;
}

.list-group-item.active {
    background-color: var(--blueGray) !important;
    border-color: white !important;
}

/*when screen gets is smaller than size (sm)all = 768 px increase the font size*/
@media (max-width: 768px) {
    .experience-wrapper {
      font-size: 1.4rem !important;
    }
}
.projects-wrapper {
    position: relative;
    text-align: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
    background-color: var(--darkBlue);
}

.projects-info {
    display: flex;
    justify-content: center;
}

.projects-info h3 {
    color: var(--lightGreen);
}

.project-description {
    font-size: 1.4rem;
    background-color: var(--midnightBlue);
    padding-right: 1rem;
    border-radius: 1rem;
    padding-top: .8rem;
    padding-bottom: .8rem;
}

.project-description p {
    margin: 0;
}

.tools-button {
    justify-content: space-evenly;
    align-items: center;
    padding-top: .5rem;
}

.btn-light {
    font-weight: 1000 !important;
    color: var(--darkBlue) !important;
    border-color: var(--darkBlue) !important;
}

.btn-light:hover {
    font-weight: 1000 !important;
    background-color: var(--darkBlue) !important;
    color: white !important;
    border-color: white !important;
}

.card {
    background-color: var(--midnightBlue)!important;
    border-color: white !important;
    margin-top: .8rem;
    text-align: start;
}

.projects-info a:link, .projects-info a:visited, .projects-info a:active{
    color: white;
    font-weight: 900;
}

.projects-info a:hover {
    color: var(--midnightBlue);
    background-color: white;
}
.contact-wrapper {
    position: relative;
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
    background-color: var(--darkBlue);
    opacity: .85;
}

.contact-info {
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer-note {
    display: flex;
    justify-content: center;
    flex-direction: column;
    font-size: 1rem;
    font-weight: 600;
}

.footer-note a {
    color: white;
    text-decoration: none;
}

.footer-note a:hover {
    color: var(--lightGreen);
    text-decoration: underline;
}

.social-media-list a{
    color: white;
}

.social-media-list a:hover {
    border-bottom: 0.2rem solid var(--lightGreen);
    color: var(--lightGreen);
}

.footer .logo {
    border-bottom: 0.2rem solid var(--darkBlue);
  }
  
.footer .logo:hover {
    border-bottom: 0.2rem solid var(--lightGreen);
}
