.logo {
    width: 8rem;
}

.navbar {
    padding-left: 5rem;
    padding-right: 5rem;
    background-color: var(--darkBlue);
    opacity: .85;
}

nav a.nav-link {
    font-weight: 500;
    letter-spacing: 0.0035rem;
    cursor: pointer;   
}

/*!important overrides bootstrap's rule*/
.navbar-nav > li > a {
    text-transform: uppercase;
    color: white !important;
    margin-left: .6rem;
    margin-right: .6rem;
    border-top: 0.2rem solid var(--darkBlue);
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus, .navbar-nav > li > a:hover{
    border-top: 0.2rem solid var(--lightGreen);
    color: var(--lightGreen) !important;
}

.navbar-toggler {
    color:white !important;
}

.navbar .logo {
    border-top: 0.2rem solid var(--darkBlue);
  }
  
.navbar .logo:hover {
    border-top: 0.2rem solid var(--lightGreen);
}